.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(196, 59, 189, 0.2);
  position: fixed;
  top:0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(0);
 
  pointer-events: none;
}

.modal.active {
  transform: scale(1);
  pointer-events: all;

}
.modal__content {
  padding: 20px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 12;
  }